import Vuex from '@/store/index.js'

export function univerifyLogin() {
	const commit = Vuex.commit;
	const PROVIDER = 'univerify';

	/**
	 * reject(true) 
	 * reject(false) 
	 */
	return new Promise((resolve, reject) => {
		uni.getProvider({
			service: 'oauth',
			success: (res) => {
				if (res.provider.indexOf(PROVIDER) !== -1) {
					// APP onLaunch
					uni.login({
						provider: PROVIDER,
						success: (res) => {
							uni.closeAuthView();
							uni.showLoading();

							uniCloud.callFunction({
								name: 'user-center',
								data: {
									action: 'loginByUniverify',
									params: res.authResult
								},
								success: (e) => {
									console.log('login success', e);

									if (e.result.code == 0) {
										const username = e.result.username || e.result.mobile || ''

										uni.setStorageSync('uni_id_token', e.result.token)
										uni.setStorageSync('username', username)
										uni.setStorageSync('login_type', 'online')

										commit('login', username)
										resolve();

										uni.switchTab({
											url: '../main/main',
										});
									} else {
										uni.showModal({
											title: `: ${e.result.code}`,
											content: e.result.message,
											showCancel: false
										})
										console.log('', e);

										e.result.errMsg = e.result.message;
									}
								},
								fail: (e) => {
									uni.showModal({
										title: ``,
										content: e.errMsg,
										showCancel: false
									})
								},
								complete: () => {
									uni.hideLoading()
								}
							})
						},
						fail: (err) => {
							console.error('' + JSON.stringify(err));

							// 
							if (err.code == 30002) {
								uni.closeAuthView();
								reject(true);
								return;
							}
							
							// 
							if (err.code == 30003) {
								uni.closeAuthView();
								reject(false);
								return;
							}

							reject(err);
						}
					})
				} else {
					reject();
				}
			},
			fail: (err) => {
				console.error('' + err.errMsg);
				reject(err)
			}
		});
	})
}

export function univerifyErrorHandler(err, cb) {
	if (!err) {
		cb && cb()
		return
	};

	const state = Vuex.state;
	const obj = {
		/* showCancel: true,
		cancelText: '',
		success(res) {
			if (res.cancel) {
				cb && cb()
			}
		} */
	}

	switch (true) {
		// 
		case err.code == 1000:
			uni.showModal(Object.assign({
				title: ``,
				content: `${err.errMsg}${err.code}\nhttps://ask.dcloud.net.cn/article/37965`,
			}, obj));
			break;
			// 
		case err.code == 30005:
			uni.showModal(Object.assign({
				showCancel: false,
				title: ``,
				content: `${err.errMsg}${err.code}`
			}, obj));
			break;
			//
		case err.code != 30003:
			uni.showModal(Object.assign({
				showCancel: false,
				title: ``,
				content: `${err.errMsg}${err.code}`,
			}, obj));
			break;
	}
}
